'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import type { INews } from "@/type/Types"
import styles from './edit.module.scss'

interface EditNewProps {
  news: INews
}

const EditNew = ({ news }: EditNewProps) => {
  const router = useRouter()
  const [formData, setFormData] = useState({
    title: news.title_one || '',
    description: news.description || '',
    date: news.date || '',
    image_url: news.image_url || ''
  })
  const [isLoading, setIsLoading] = useState(false)

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    
    try {
      const token = localStorage.getItem('access_token')
      const response = await fetch(`http://localhost:9091/api/news/${news.id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          title_one: formData.title,
          description: formData.description,
          date: formData.date,
          image_url: formData.image_url
        })
      })

      if (!response.ok) throw new Error('Ошибка сохранения')
      router.push('/news')
    } catch (error) {
      console.error('Ошибка:', error)
      alert('Не удалось сохранить изменения')
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Редактировать новость</h1>
      
      <form onSubmit={handleSubmit} className={styles.form}>
        <div className={styles.formGroup}>
          <label className={styles.label}>Заголовок</label>
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleChange}
            className={styles.input}
            required
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.label}>Описание</label>
          <textarea
            name="description"
            value={formData.description}
            onChange={handleChange}
            rows={4}
            className={styles.textarea}
            required
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.label}>Дата</label>
          <input
            type="date"
            name="date"
            value={formData.date}
            onChange={handleChange}
            className={styles.input}
            required
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.label}>Ссылка на изображение</label>
          <input
            type="text"
            name="image_url"
            value={formData.image_url}
            onChange={handleChange}
            className={styles.input}
          />
        </div>

        <div className={styles.buttonGroup}>
          <button
            type="submit"
            disabled={isLoading}
            className={styles.submitButton}
          >
            {isLoading ? 'Сохранение...' : 'Сохранить'}
          </button>
          
          <button
            type="button"
            onClick={() => router.push('/news')}
            className={styles.cancelButton}
          >
            Отмена
          </button>
        </div>
      </form>
    </div>
  )
}

export default EditNew